html {
    scroll-behavior: smooth;
}

.App {
    background-color: white;
    background-size: 16px 16px;
    z-index: 1;
    position: absolute;
    width: 100%;
}

.navBar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    height: 40px;
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    backdrop-filter: blur(100px);
    background-color: rgba(220, 220, 220, 0.8);

}

.navButton {
    text-align: center;
    color: black;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 1rem;
    line-height: 40px;
    width: 50%;
    text-decoration: none;
}

.productsList {
    position: relative;
    margin-top: 150px;
    color: white;
    font-size: 20px;
    font-weight: bold;
    left: 5%;
    width: 90%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 100px;
}

.productMini {
    display: block;
    position: relative;
    text-align: center;
    margin: 15px;
    color: black;
    flex: 0 0 250px;
    justify-content: space-evenly;
    height: 300px;
    z-index: 8;
    text-decoration: none;
}

.ratingAnchor {
    position: absolute;
    top: 150px;
}

.fillMeter {
    position: absolute;
    z-index: 5;
}

.starRating {
    position: absolute;
    z-index: 6;
}

.productMini > img {
    position: absolute;
    z-index: 10;
    top: 50px;
    left: 0;
    border-radius: 20px;
}

.productMini > h1 {
    margin-top: 3px;
    margin-bottom: 170px;
}

.productMini > img:hover {
    opacity: 10%;
    transition: opacity .5s ease-in-out;
    -moz-transition: opacity .5s ease-in-out;
    -webkit-transition: opacity .5s ease-in-out
}


.product {
    text-align: center;
    position: relative;
    top: 100px;
    color: #090909;
    font-size: 20px;
    font-weight: bold;
    left: 5%;
    width: 90%;
}

.productImage {
    position: absolute;
    width: 100%;
    left: 0;
    padding-bottom: 100px;
}

.hoverImage:active{
    opacity: 0;
}


